@import"https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap";.admin-panel{display:flex;flex-direction:column;min-height:100vh;background-color:#f8f9fa;font-family:"Poppins",sans-serif}.admin-header{display:flex;justify-content:space-between;align-items:center;padding:15px 30px;background-color:#112d4e;color:#fff;box-shadow:0 2px 10px rgba(0,0,0,.1)}.admin-header h1{margin:0;font-size:1.8rem;font-weight:600;display:flex;align-items:center}.admin-header h1 svg{margin-right:10px}.admin-logout-btn{background-color:rgba(231,76,60,.8);color:#fff;border:none;padding:8px 16px;border-radius:4px;font-size:.9rem;font-weight:500;cursor:pointer;transition:all .3s ease}.admin-logout-btn:hover{background-color:#e74c3c;box-shadow:0 4px 8px rgba(231,76,60,.3)}.admin-main{display:flex;flex:1}.admin-sidebar{width:250px;background-color:#112d4e;padding:20px 0;color:#f9f7f7;box-shadow:2px 0 10px rgba(0,0,0,.1)}.admin-info{padding:20px;border-bottom:1px solid hsla(0,0%,100%,.1);display:flex;flex-direction:column;align-items:center}.admin-info .admin-avatar{width:70px;height:70px;background-color:#3f72af;color:#fff;border-radius:50%;display:flex;align-items:center;justify-content:center;font-size:1.8rem;font-weight:600;margin-bottom:10px;box-shadow:0 4px 10px rgba(0,0,0,.2);border:2px solid hsla(0,0%,100%,.2)}.admin-info .admin-role{font-size:.9rem;color:hsla(0,0%,100%,.6);margin-top:5px}.admin-nav{list-style:none;padding:0;margin:20px 0 0 0}.admin-nav li{margin-bottom:5px}.admin-nav li.active button{background-color:#3f72af;box-shadow:0 4px 8px rgba(0,0,0,.2)}.admin-nav li.active button::before{opacity:1;transform:translateX(0)}.admin-nav button{display:block;width:100%;text-align:left;padding:12px 20px;background:none;border:none;color:#f9f7f7;font-size:1rem;cursor:pointer;transition:all .3s ease;position:relative}.admin-nav button::before{content:"";position:absolute;left:0;top:0;height:100%;width:4px;background-color:#f9f7f7;opacity:0;transform:translateX(-5px);transition:all .3s ease}.admin-nav button:hover{background-color:hsla(0,0%,100%,.1)}.admin-content{flex:1;padding:30px;position:relative}.content-header{display:flex;justify-content:space-between;align-items:center;margin-bottom:30px}.content-header h2{margin:0;font-size:1.6rem;color:#112d4e;display:flex;align-items:center}.content-header h2 svg{margin-right:10px}.content-header .search-box{position:relative}.content-header .search-box input{width:280px;padding:10px 15px;padding-left:40px;border:1px solid rgba(0,0,0,.1);border-radius:8px;font-size:.95rem;transition:all .3s ease}.content-header .search-box input:focus{outline:none;border-color:#3f72af;box-shadow:0 0 0 3px rgba(63,114,175,.1)}.content-header .search-box .search-icon{position:absolute;left:15px;top:50%;transform:translateY(-50%);color:#7f8c8d}.users-table-container{overflow-x:auto;background:#fff;border-radius:10px;box-shadow:0 4px 15px rgba(0,0,0,.05)}.users-table{width:100%;border-collapse:collapse}.users-table th,.users-table td{padding:12px 15px;text-align:left;border-bottom:1px solid rgba(0,0,0,.05)}.users-table th{background-color:#f8f9fa;color:#112d4e;font-weight:600;font-size:.9rem}.users-table th:first-child{border-top-left-radius:10px}.users-table th:last-child{border-top-right-radius:10px}.users-table tbody tr{transition:all .3s ease}.users-table tbody tr:hover{background-color:rgba(63,114,175,.03)}.users-table tbody tr.selected-row{background-color:rgba(231,76,60,.05);border-left:3px solid #e74c3c}.users-table td{color:#333;font-size:.95rem}.user-avatar-small{width:36px;height:36px;border-radius:50%;overflow:hidden;display:flex;align-items:center;justify-content:center}.user-avatar-small img{width:100%;height:100%;object-fit:cover}.user-avatar-small .avatar-placeholder{width:100%;height:100%;background-color:#dbe2ef;color:#112d4e;display:flex;align-items:center;justify-content:center;font-weight:600}.status-badge{display:inline-block;padding:4px 8px;border-radius:12px;font-size:.8rem;font-weight:500}.status-badge.online{background-color:rgba(46,204,113,.1);color:#27ae60}.status-badge.offline{background-color:rgba(149,165,166,.1);color:#7f8c8d}.action-buttons{display:flex;gap:8px}.action-buttons button{padding:5px 12px;border-radius:4px;font-size:.85rem;cursor:pointer;transition:all .3s ease;border:none;font-weight:500}.action-buttons button.delete-button{background-color:rgba(231,76,60,.1);color:#e74c3c}.action-buttons button.delete-button:hover{background-color:rgba(231,76,60,.2)}.loading-spinner{text-align:center;padding:40px;color:#7f8c8d;font-size:1.1rem}.no-user-message{text-align:center;color:#7f8c8d;padding:20px 0}.delete-confirmation-modal{position:fixed;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,.5);display:flex;align-items:center;justify-content:center;z-index:1000}.modal-content{background-color:#fff;padding:30px;border-radius:10px;width:90%;max-width:500px;box-shadow:0 10px 30px rgba(0,0,0,.2)}.modal-content h3{margin-top:0;color:#112d4e;font-size:1.4rem}.modal-content p{color:#333;line-height:1.5}.modal-content .warning-text{color:#e74c3c;font-weight:500}.modal-actions{display:flex;justify-content:flex-end;gap:15px;margin-top:25px}.modal-actions button{padding:10px 20px;border-radius:6px;font-size:1rem;font-weight:500;cursor:pointer;transition:all .3s ease;border:none}.modal-actions button.cancel-button{background-color:#f5f5f5;color:#333}.modal-actions button.cancel-button:hover{background-color:#e0e0e0}.modal-actions button.confirm-delete-button{background-color:#e74c3c;color:#fff}.modal-actions button.confirm-delete-button:hover{background-color:rgb(213.698630137,43.8356164384,26.301369863);box-shadow:0 4px 10px rgba(231,76,60,.3)}.success-message{background-color:rgba(46,204,113,.1);color:#27ae60;padding:12px 15px;border-radius:8px;margin-bottom:20px;font-size:.9rem;display:flex;align-items:center;border-left:3px solid #27ae60}.success-message::before{content:"✓";margin-right:10px;font-weight:bold}.error-message{background-color:rgba(231,76,60,.1);color:#e74c3c;padding:12px 15px;border-radius:8px;margin-bottom:20px;font-size:.9rem;display:flex;align-items:center;border-left:3px solid #e74c3c}.error-message::before{content:"⚠️";margin-right:10px}