@import"https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap";.auth-container{display:flex;justify-content:center;align-items:center;min-height:100vh;background:linear-gradient(135deg, var(--accent-color), var(--bg-primary));font-family:"Poppins",sans-serif;padding:20px}.auth-container::before{content:"";position:absolute;top:-50px;left:-50px;width:200px;height:200px;background:var(--shadow-color);border-radius:50%;z-index:0}.auth-container::after{content:"";position:absolute;bottom:-80px;right:-80px;width:300px;height:300px;background:var(--shadow-color);border-radius:50%;z-index:0}.auth-form-container{width:100%;max-width:520px;padding:2.5rem;background:var(--bg-secondary);border:1px solid var(--border-color);box-shadow:0 10px 30px var(--shadow-color);border-radius:16px;z-index:1;overflow:hidden;position:relative}.auth-form-container::before{content:"";position:absolute;top:-10px;left:-10px;right:-10px;bottom:-10px;z-index:-1;background:linear-gradient(45deg, transparent, var(--shadow-color), transparent);transform:translateY(-100%);animation:wave 8s infinite linear}.auth-form-container h2{color:var(--text-primary);text-align:center;margin-bottom:.5rem;font-size:2rem;font-weight:700}.auth-form-container h3{color:var(--text-secondary);text-align:center;margin-bottom:2rem;font-weight:400;position:relative}.auth-form-container h3::after{content:"";position:absolute;width:60px;height:3px;background:var(--accent-color);bottom:-10px;left:50%;transform:translateX(-50%);border-radius:3px}.form-group{margin-bottom:1.5rem;position:relative}.form-group label{display:block;margin-bottom:.5rem;font-weight:500;color:var(--text-primary);transition:all .3s ease}.form-group input{width:100%;padding:12px 15px;border:1px solid var(--border-color);border-radius:8px;font-size:1rem;transition:all .3s ease;background-color:var(--input-bg);color:var(--text-primary)}.form-group input:focus{border-color:var(--accent-color);outline:none;box-shadow:0 0 0 3px var(--shadow-color)}.form-group input::placeholder{color:var(--text-secondary);opacity:.7}.form-row{display:flex;gap:1.2rem}.form-row .form-group{flex:1}.btn-primary{width:100%;padding:14px;background:var(--accent-color);color:var(--button-text);border:none;border-radius:8px;font-size:1rem;font-weight:600;cursor:pointer;margin-top:1.5rem;transition:all .3s ease;position:relative;overflow:hidden}.btn-primary:hover{background-color:rgb(49.5,89.5714285714,137.5);transform:translateY(-2px);box-shadow:0 5px 15px rgba(0,0,0,.2)}.btn-primary:active{transform:translateY(0)}.error-message{background-color:rgba(231,76,60,.1);color:#e74c3c;padding:12px;border-radius:8px;margin-bottom:1.5rem;font-size:.9rem;border-left:3px solid #e74c3c;display:flex;align-items:center}.error-message::before{content:"⚠️";margin-right:10px;font-size:1.2rem}.auth-footer{text-align:center;margin-top:2rem;color:var(--text-secondary)}.auth-footer a,.auth-footer .text-link{color:var(--accent-color);text-decoration:none;font-weight:500;margin-left:5px;transition:all .3s ease;border:none;background:none;cursor:pointer;padding:0}.auth-footer a:hover,.auth-footer .text-link:hover{text-decoration:underline}@keyframes wave{0%{transform:translateY(-100%) rotate(0deg)}100%{transform:translateY(100%) rotate(360deg)}}@keyframes ripple{0%{transform:scale(0, 0);opacity:1}20%{transform:scale(25, 25);opacity:1}100%{opacity:0;transform:scale(40, 40)}}@media(max-width: 600px){.form-row{flex-direction:column;gap:0}.auth-form-container{padding:1.5rem}}.input-icon{margin-right:8px;vertical-align:middle;color:var(--accent-color);font-size:.9rem}.dark .form-group label{color:var(--text-primary)}.dark .form-group input{background-color:var(--input-bg);color:var(--text-primary);border-color:var(--border-color)}.dark .form-group input:focus{border-color:var(--accent-color);box-shadow:0 0 0 3px rgba(79,148,224,.2)}.dark .form-group input::placeholder{color:var(--text-secondary);opacity:.7}.dark .btn-primary{background-color:var(--accent-color);color:var(--button-text)}.dark .btn-primary:hover{background-color:rgb(37.7391304348,121.7391304348,214.2608695652);box-shadow:0 5px 15px rgba(0,0,0,.3)}.dark .auth-footer a,.dark .auth-footer .text-link{color:var(--accent-color)}.dark .auth-footer a:hover,.dark .auth-footer .text-link:hover{color:rgb(122.3623188406,174.3623188406,231.6376811594)}.dark .error-message{background-color:rgba(231,76,60,.15)}